body {
  font-family: 'Arimo', sans-serif;
  font-size: smaller;
}

h1, h2, h3, h4 {
  margin-bottom: 12px;
}

h1 {
 font-size: 28px;
}

h2 {
  font-size: 22px;
}

h3 {
  font-size: 20px;
}

h4 {
  font-size: 18px;
}

p {
  font-size: 14px;
}

.container {
  padding-top: 48px;
}

section {
  margin: 32px 0;
  padding: 32px 0;
  border: 0;
  border-bottom: 1px solid rgba(0,0,0,.1);
}

.row {
  margin-bottom: 32px;
}

.profile {
  height: 135px;
  align-content: center;
}

.ptcontainer {
  width: 100%;
}

.stars {
  color: #016F4A;
}

.sens {
  color: #E51837;
}

.pens {
  color: #C5B358;
}

.y-align {
  padding-top: 20%;
  text-align: center;
  max-width: fit-content;
}

.y-align-b {
  padding-top: 40%;
  text-align: center;
}

.legend span {
  width: 20%;
  display: inline-block;
  text-align: center;
  cursor: pointer;
  color: white;
}

blockquote {
  font-family: 'PT Sans Caption', sans-serif;
  font-size: 12px;
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  padding-top: 25px;
  height: 0;
}
.videoWrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#footer, .hat {
  height: 60px;
  background-color: #016F4A;
  padding-top: 20px;
  padding-bottom: 20px;

  .container {
    padding-top: 0;
  }
}

#footer a {
  color:#efefef;
}

@media (min-width: 1200px) {
  .ptprofile {
    height: 160px;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .ptprofile {
    height: 140px;
  }
}


@media (min-width: 768px) and (max-width: 991px) {
  .ptprofile {
    height: 120px;
  }
}

@media (max-width: 767px) {
  .ptprofile {
    height: 120px;
  }
}

/* custom c3 */
/*.c3-texts .c3-text {fill-opacity: 0 !important; }*/
.c3-chart-text {opacity: 0 !important}

.lmpts .c3-line {
  stroke-width: 2.5px !important;
}

.c3-chart-text.c3-target.c3-defocused {
  opacity: 0 !important;
}

.ctitle {
  font-size: large;
}